import { request } from '../../request.js'
import Loading from '../../common/loading/loading.vue'
export default {
	data() {
		return {
			res:'',
			dataList: [],
			loading: false,
			id: ''
		};
	},
	components: {
		Loading
	},
	onShow() {
		this.getUserGroup()
	},
	methods: {
		// 获取我的团队资料
		getUserGroup() {
			this.loading = false;
			request({
				url: 'index.php?s=/wap/member/getTeamMembers',
				method: 'post'
			}).then(res => {
				this.loading = true;
				// 等级
				this.id = res.data.level_id;
				this.res = res;
				const arr = []
				const list = res.data.list;
				for(let key in list) {
					arr.push({name: key,list:[list[key]]});
				};
				this.dataList = arr;
			})
		},
		goGroup(id,name) {
			uni.navigateTo({
				url:'/pages/components/my-group/my-group?id='+id+'&name='+name
			})
		}
	}
}
